import action from "../../store/actions";
import React, { Component } from "react";
import { connect } from "react-redux";
import { RouteComponentProps } from "react-router";
import { message, Modal, Empty } from "antd";
import "./index.less";
import { addNewProject, copyProject, getExamList } from "../../apis/examEdit";

interface IProps extends RouteComponentProps {
  login_id: number;
  setExamId: (payload: { exam_id: number }) => void;
  setStepStatus: (payload: { index: number }) => void;
}
interface IProject extends IExamItem {
  question_list?: string;
}
interface IState {
  examSelectWindow: boolean;
  examList: IProject[];
}
class AddProject extends Component<IProps, IState> {
  state = {
    examSelectWindow: false,
    examList: [],
  };
  newProject = async () => {
    const { login_id, setExamId, setStepStatus } = this.props;
    const addRes = await addNewProject({ creator_id: login_id });
    if (addRes && addRes.code === "1001") {
      setStepStatus({ index: 0 });
      setExamId({ exam_id: addRes.exam_id });
      this.props.history.push("/layout/examination");
    } else {
      message.warn("出错了");
    }
  };
  getList = async () => {
    const examListRes = await getExamList({ creator_id: this.props.login_id });
    if (examListRes && examListRes.code === "1001" && examListRes.examList) {
      const tempList = examListRes.examList.sort(
        (a, b) =>
          new Date(b.update_time).getTime() - new Date(a.update_time).getTime()
      );
      this.setState({ examList: tempList });
    }
  };
  render() {
    const { newProject } = this;
    const { setStepStatus, setExamId } = this.props;
    const { examSelectWindow, examList } = this.state;
    return (
      <div className="add-pro-page">
        <span
          className="back iconfont icon-houtui1"
          onClick={() => {
            this.props.history.goBack();
          }}
        ></span>
        <div className="create-type">
          <div className="title">选择创建方式</div>
          <div className="content">
            <div className="blank" onClick={newProject}>
              <h3>
                <img src="http://192.168.5.104:5008/img/add-project.png" alt="" />
                <p>空白创建</p>
                <p>从0搭建考试</p>
              </h3>
            </div>
            <div
              className="copy"
              onClick={() => {
                this.getList();
                this.setState({ examSelectWindow: true });
              }}
            >
              <h3>
                <img src="http://192.168.5.104:5008/img/copy-project.png" alt="" />
                <p>复制项目</p>
                <p>复制已创建项目</p>
              </h3>
            </div>
            <Modal
              visible={examSelectWindow}
              title="项目选择"
              style={{ minWidth: 740, maxWidth: 1000 }}
              bodyStyle={{
                minHeight: 200,
                padding: "18px 30px 12px",
              }}
              centered
              footer={null}
              onCancel={() => {
                this.setState({ examSelectWindow: false });
              }}
            >
              <ul className="exam-select">
                {examList.length > 0 ? (
                  examList.map((item: IProject) => (
                    <li key={item.id}>
                      <p>{item.exam_name}</p>
                      <p className="question_count">
                        共{item.question_list?.split(",").length}题
                      </p>
                      <div
                        className="float"
                        onClick={async () => {
                          const copyRes = await copyProject({
                            exam_id: item.id,
                            creator_id: this.props.login_id,
                          });
                          // console.log(copyRes);
                          if (copyRes.code === "1001") {
                            setStepStatus({ index: 0 });
                            setExamId({ exam_id: copyRes.insertId });
                            this.props.history.push("/layout/examination");
                          }
                        }}
                      >
                        复制项目
                      </div>
                    </li>
                  ))
                ) : (
                  <Empty description="暂无内容" />
                )}
              </ul>
            </Modal>
          </div>
        </div>
      </div>
    );
  }
}
interface IMapStateToProps {
  examEdit: object;
}
export default connect(
  (state: IMapStateToProps) => state.examEdit,
  action.examEdit
)(AddProject);
